body {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.addUser {
  background-color: white;
  width: 25%;
  margin: 50px auto;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 1px 1px 8px rgba(0, 0, 0, 0.065);
}

.addUserForm .inputGroup {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.addUserForm .inputGroup label {
  margin-top: 10px;
}

.addUserForm .inputGroup input {
  margin-top: 5px;
  padding: 10px;
}

.inputGroup button {
  margin-top: 10px;
}

.login {
  margin-top: 30px;
  text-align: center;
}

.login .btn.btn-primary {
  width: 50%;
}

h3 {
  text-align: center;
  font-weight: bold;
  color: black;
}

button {
  padding: 0.5rem;
  background-color: blue;
  color: white;
  border-radius: 0.5rem;
  border: none;
}

input {
  border-radius: 0.25rem;
  border-width: 1px;
}

p {
  color: red;
  font-size: 12px;
}

.have-account {
  color: black;
  font-size: 16px;
}
